import React, { useState, useEffect } from "react";
import Navbar from "./comps/navbar";
import "../public/static/css/post.scss";

const Post = () => {

    const [docName, setDocName] = useState("Untitled Document");

    const handleDocNameChange = e => {
        setDocName(e.target.value);
    }

    const highlightDocName = e => {
        e.target.select();
    }

    // Set "anchors" around selected text

    const getSelection = e => {
        let text = e.target.value;
        
        console.log(window.getSelection().toString())
        console.log(text.indexOf(window.getSelection().toString()))
    }

    return (
        <div className="postMother">
            <Navbar/>
            <div className="postMain">
                <div className="editOptionsParent">
                <div className="docNameInputDiv">
                    <input onClick={e => highlightDocName(e)} value={docName} onChange={e => handleDocNameChange(e)} className="docNameInput" placeholder="Untitled Document"/>
                </div>
                </div>
                <div className="postInputParent">
                    <textarea onMouseUp={e => getSelection(e)} placeholder="Begin typing here..." minlength="10"></textarea>
                </div>
            </div>
        </div>
    )
}

export default Post